.footer {
    background-color: #313552;
}

.footerWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.footerText{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}
.FooterButton{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.FooterButtonIn {
    border: 1px solid #FFC576;
    color: #FFC576;
    background: transparent;
    text-transform: uppercase;
    font-weight: 600;
    padding: 10px 30px;
    border-radius: 2px;
    font-size: 14px;
    display: inline-block;
    text-decoration: none;
}